<template>
	<div>
    <el-row>
      <el-col :span="6" v-for="finding in raw" :offset="1" >
        <el-card class="box-card" shadow="hover" :body-style="{ padding: '0px' }" @click.native="toFinding(finding.id)">
          <img src="http://35.240.33.107:8088/Storage/a5428cfe-2bd8-495b-a8f5-210773855d34.jpg">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>Kiwrious Team</div>
            <div class="box-name">{{finding.name}}</div>
          </div>
        </el-card>
      </el-col>
    </el-row>  
  </div>
</template>

<script>
	import axios from 'axios'

  const storage = {}
  const api = axios.create({
    baseURL: 'https://887c830a-6545-4ec1-8ec8-f3085930ba42.mock.pstmn.io/api',
  })
  export default {
    data() {
      return {
        raw: null,

      }
    },
    beforeRouteEnter(to, from, next) {
      const url = '/finding/inquiry/948a06f5-5f03-498a-9258-3f89ea1e2362'
      api.get(url)
      .then((response) => {
        console.log(response)
        storage.list = response.data.findings;
        next()
      })
      .catch((error) => {
        console.log(error)
        this.$message({
          message: 'Network Error, Please try again',
          type: 'error'
        })
      })
    },
    created() {
      this.raw = storage.list
    },
    methods: {
      toFinding(fid) {
        this.$router.push({path: '/finding'})
      }
    }
  }
</script>

